@keyframes move {
  20% {
    transform: translateY(3px);
    animation-timing-function: ease-in-out;
  }

  75% {
    transform: translateY(-3px);
  }
}

._scroll-up {
  position: fixed;
  top: 35%;
  right: 15px;
  z-index: -1;
  width: 48px;
  height: 48px;
  background-color: rgba(35, 182, 145, 0.7);
  background-image: url("../images/icons/arrow.svg");
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.3s ease 0s;
  transform: rotate(180deg);
}

._scroll-up:hover {
  background-color: #2aceab;
}

._scroll-up--show {
  z-index: 3;
  opacity: 1;
}

._disable-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.slick-slide {
